<template>
	<view id="template" class="container">
		<view v-if="item && item.headerTextResult" class="parser">
			<parser :html="item.headerTextResult.text" ref="header" />
		</view>
		<view v-if="item && item.headerTextResult && item.chartResult" class="spliter"></view>
		<view v-if="item && item.chartResult" class="chart-cavans-container" :style="[chartContainerStyle]">
			<!--#ifndef MP-ALIPAY -->
			<canvas :canvas-id="canvasId" :id="canvasId" class="chart-canvas" :style="[canvasStyle]"
				@touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd" />
			<!--#endif-->
		</view>
		<view v-if="item && item.footerTextResult" class="spliter"></view>
		<view v-if="item && item.footerTextResult" class="parser">
			<parser :html="item.footerTextResult" ref="footer" />
		</view>
	</view>
</template>

<script>
	import parser from "@/components/parser";
	import uCharts from '@/packageSubPage/components/u-charts/u-charts';
	import { getChartConfig } from '@/packageSubPage/common/helpers/chartHelper';
	
	const canvasMap = {};
	
	let canvasIdSeed = 0;

	export default {
		name: 'chartElement',
		components: { parser },
		data() {
			return {
				id: null,
				canvasId: `canvasId_${canvasIdSeed++}`,
				touchEndEvent: () => {},
			};
		},
		props: {
			item: {
				type: Object,
				default() {
					return {
						headerTextResult: null,
						footerTextResult: null,
						chartResult: {},
						chartTemplate: '',
						chartType: ''
					};
				}
			},
			chartConfig: { type: Object, default: () => {} },
			chartContainerStyle: { type: Object, default: () => {} },
			canvasStyle: { type: Object, default: () => { return {width: '750rpx', height: '500rpx'};} },
		},
		mounted() {
			this.loadCharts();		
		},
		watch: {
			item: {
				handler: function(value) {
					this.loadCharts();          		
				},
				immediate: true,
				deep: true
			},
		},
		methods: {
			loadCharts() {
				const canvasId = this.canvasId;
				if(!this.item || !this.item.chartResult || !this.canvasId) {
					return;
				}
				const { series, categories, basicData, chartTemplate, centerIndex, chartType } = this.item.chartResult;
				const { config, touchEnd } = getChartConfig(series, basicData, categories, chartTemplate, centerIndex, chartType);
				this.touchEndEvent = touchEnd;
				if(canvasMap[canvasId]) {
					canvasMap[canvasId].updateData({ series: config.series, categories: config.categories });
				} else {					
					canvasMap[canvasId] = new uCharts({
						...config,
						$this: this, // 此处必须为当前最新的this，否则无法找到画布。
						canvasId,
						...this.chartConfig
					});
				}
				
			},
			touchStart(e){
				const canvas = canvasMap[this.canvasId];
				canvas.scrollStart(e);
				this.$emit('touchStart', e, canvas);
			},
			touchMove(e) {
				const canvas = canvasMap[this.canvasId];
				canvas.scroll(e);
				this.$emit('touchMove', e, canvas);
			},
			touchEnd(e) {
				const canvas = canvasMap[this.canvasId];
				canvas.scrollEnd(e);
				this.$emit('touchEnd', e, canvas);
				this.touchEndEvent(e, canvas);
			},
			toJSON() {},
		}
	}
</script>

<style scoped>
	.container {
		display: flex;
		flex-direction: column;
		width: 100%;
		flex: 1;
		box-sizing: border-box;
	}
	.parser {
		width: auto;
		position: relative;
		padding: 0px 36upx 26upx 36upx;
		justify-content: center;
		font-size: 32upx;
		line-height: 52upx;
	}
	.chart-cavans-container {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #FFFFFF;
	}
	
	.spliter {
		height: 36upx;
		width: 100%;
		background-color: #FFFFFF;
	}
</style>
